<template>	
<section class="wrap has_header_wrap c_wrap">
	<!-- 头部标题 -->
	<com-header titles="需求登记">
		<template  slot="style-def"></template>
	</com-header>
	<!-- 头部标题 end -->
	
	<div class="register_list">
		<div class="item" v-for="(lt,index) in list">
			<div class="rel_title">
				<span>提交需求时间：</span><span>{{ lt.operationDate }}</span>
			</div>
			<ul class="rel_list">
				<li class="d_flex">
					<div class="rel_img">
						<img v-lazy="lt.product.photodURL" :alt="lt.productName">
					</div>
					<div class="right">
						<h2 class="d_flex"><span>{{ lt.productName }}</span><span>￥{{ lt.product.ourPrice }}</span></h2>
						<p class="d_flex"><span>{{ lt | specFilter }}</span><span>×{{ lt.qty }}</span></p>
					</div>
				</li>
			</ul>
			<div class="rel_info">
				<p class="d_flex"><span>登记的姓名：</span><span>{{ lt.realName }}</span></p>
				<p class="d_flex"><span>登记的手机：</span><span>{{ lt.phone }}</span></p>
				<p class="d_flex"><span>登记的留言：</span><span>{{ lt.remark }}</span></p>
			</div>
			<div class="btns">
				<mt-button 
					class="green" 
					size="normal" 
					plain
					@click="restConfirm(lt,index)">重新提交需求</mt-button>
			</div>
		</div>
	</div>

	<!-- 没有更多数据 -->
	<div id="nothing" v-if="isNothing">
		<p>
			<span>收藏夹还空空如也哦</span>
			<router-link to="">去逛逛</router-link>
		</p>
	</div>
	<!-- 没有跟多数据 end -->
</section>
</template>

<script>
import comHeaderTitle from '@/components/Common/ComHeaderTitle';
import comFoot from '@/components/Common/ComFoot';
import { Checklist, Button, MessageBox, Toast } from 'mint-ui';

export default {
	name: 'info',
	data (){
		return {
			isUnscRollable: false, //是否页面不可滚动
			backParentPage: false, //是否回到父页面
			isNothing: false, // 是否显示没数据
			list: [],//需求登记列表
		}
	},
	components: {
		comHeaderTitle, // 公共头部标题
		'mt-checklist': Checklist,
		'mt-button': Button,
	},
	filters: {
		//规格过滤
		specFilter ( data ){
			// console.log('spec: ', data)
			if (data.product.isMultSpec){ //是多规格
				return data.productDetailCode?data.productDetailCode.propertyAndValue:'';
			}else { //不是多规格
				return data.product.spec;
			}
		},
	},
	created (){
		const _self = this;

		this.$ajax({
			// url: '/apiserver/require/getRequire',
			url: _self.webRoot + '/require/getRequire',
			data: {
				memberId: _self.$store.state.memberId
			}
		})
		.then(function(res){
			// console.log('ddd: ',res)
			if( !res.data.success ){
				_self.$options.methods.showToast( res.data.msg );
				return false;
			}
			// _self.list = res.data.data;
			for (var i = 0; i < res.data.data.length; i++) {
				_self.list.unshift(res.data.data[i]);
			}
				// console.log(_self.list)
		})
		.catch(function(err){
			console.log(err)
		})
	},
	mounted (){
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//重新提交处方
		restConfirm ( lt,index ){
			const _self = this;
			// console.log( lt,index );
			// return false;

			this.$router.push({path: '/toregister', query: {id: lt.productID, num: lt.qty, uName: lt.realName, uPhone: lt.phone, uRemark: lt.remark}});
		},
		chooseAll (){
			var ele = document.getElementsByName("collect_item");
			var chooseAll = document.getElementById("chooseAll");

			if( chooseAll.checked ){
				for (var i = 0; i < ele.length; i++) {
					ele[i].checked = true;
				}	
			}else{
				for (var i = 0; i < ele.length; i++) {
					ele[i].checked = false;
				}	
			}
		},
		//编辑
		edit (){
			this.editHtml = this.editHtml=='编辑'?'完成':'编辑';
			this.showBtnDelect = !this.showBtnDelect;
		},
		//删除单个
		btnDelectOne (){
			MessageBox.confirm('确定执行此操作?').then(action => {
			  // console.log('确定删除'+action)
			});
		},
		//删除全部
		declectAll (){
			MessageBox.confirm('确定执行此操作?').then(action => {
			  // console.log('确定删除')
			});	
		},
		
		//公共函数
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	},
}	
</script>

<style scoped>
@import '../../../static/css/childre_wrap.css';
@import './Css/c_style.css';
@import './Css/register.css';
</style>


